.signIn {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 80vh;
    margin: auto 0;
}

.formSignIn {
    text-align: center;
}

.formSignIn p {
    margin-bottom: 20px;
    margin-top: 20px;
}

.link {
    text-decoration: none;
    cursor: pointer;
    color: #4c4cff;

}

.link:hover {
    text-decoration: underline;
}
