.mainBurgerIngredients {
    margin: 40px 20px 0 0;
    width: 600px;
    max-height: 912px;
}

.mainBurgerIngredients h1 {
    color: #F2F2F3;
}

.tabIngredients {
    display: flex;
}

.cardsIngredient {
    max-height: 800px;
    overflow-y: auto;
    scrollbar-color: #8585AD #1C1C21;
    scrollbar-width: thin;
}

.ulIngredient {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding-inline-start: 0;
}
